import React from 'react';
import "./endzone-text-input.css"

class EndzoneTextInput extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            value: ""
        }
    }

    getValue = () => {
        return this.state.value;
    }

    setValue = (new_value) => {
        this.setState({value: new_value});
    }

    render() {
        return(
            <div className='endzone-text-input-div' style={this.props.classStyle}>
                <label className='endzone-text-label' style={this.props.labelStyle}>{this.props.label}</label>
                <input className='endzone-text-input' onChange={this.props.changeCallback} id = {this.props.id} style={this.props.textStyle}></input>
            </div>
        );
    }
}



export default EndzoneTextInput;