import React from 'react';
import "./endzone-combo-box.css"


class EndzoneComboBox extends React.Component {
    constructor (props) {
        super(props);
    }
    render() {
        return(
            <div className='endzone-combo-div' style={this.props.classStyle}>
                <label className='endzone-text-label' style={this.props.labelStyle}>{this.props.label}</label>
                <select className='endzone-comboBox' id = {this.props.id} style={this.props.comboStyle} onChange={this.props.changeCallback} value={this.props.value} defaultValue={this.props.default}>
                    {this.props.options ? this.props.options.map((item, index) => <option value={item.value}>{item.text}</option>) : ''}
                </select>
            </div>
        );
    }
}



export default EndzoneComboBox;