import React from 'react';
import axios from 'axios';
import "./profile.css";
import EndzoneNav from "../../components/navigators/endzone-nav/EndzoneNav";
import EndzoneTextInput from "../../components/form-inputs/text-input/endzone-text-input";
import EndzoneComboBox from '../../components/form-inputs/endzone-comboBox/endzone-combo-box';
import PhoneInput from "react-phone-number-input";
import 'react-phone-number-input/style.css';
import queryString from 'query-string';

import { Alert } from '@mui/material';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';

class Profile extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            firstName: "",
            lastName: "",
            email: "", 
            phone: "", 
            curTeam: "",
            curTeamText: "",
            input: "", 
            teamsList: [], 
            promiseStatus: false, 
            data: null,
            alert: {
                style: {display: "none"},
                severity: "error",
                userMessage: ""
            },
        }
    }


    componentDidMount = () => {
        this.getProfileInformation();
        
        var parsedParams = queryString.parse(window.location.search);
        var action = parsedParams.action;

        if (action === 'changeTeam') {
            this.renderForm("team-form");
            this.highlightButton("team-button");
        } else {
            this.renderForm("profile-display");
            this.highlightButton("profile-button");
        }
    }

    
    getProfileInformationFiller = () => {
        this.setState({
            firstName: "Charlie",
            lastName: "Droege",
            email: "cdro@gmail.com",
            phone: "+12223334444",
            curTeam: "12345678",
            curTeamText: "Varsity",
            teamsList: [{"text": "varsity", "value": "varsity"}, {"text": "Pirates-Strawhats-Luffy-Pog", "value": "jv"}, {"text": "loser", "value": "loser"}]
        });
    }

    async getProfileInformation(e) {
        var requestOptions = {
            method: 'GET',
            url: '/endzone/account/user/profile'
        };

        await axios(requestOptions).then((response) => {
            if (response.status === 200) {
                console.log(response)
                this.setState({
                    firstName:  response.data["first_name"],
                    lastName: response.data["last_name"],
                    email: response.data["email"],
                    phone: response.data["phone"],
                    curTeam: response.data["curTeam"],
                    curTeamText: response.data["curTeamText"],
                    teamsList: response.data['teamsList']
                });
            } else {
                this.FailureMessage(response.data);
            }
        }).catch((error) => this.FailureMessage("Failure connecting to database."));
    }

    highlightButton(button) {
        document.getElementById(button).style.background = "#353b48";
        var buttonList = ["profile-button", "name-button", "email-button", "phone-button", "team-button"];
        var index = buttonList.indexOf(button);
        buttonList.splice(index, 1);
        for (var i in buttonList) {
            document.getElementById(buttonList[i]).style.background = "#0d1019";
        }
    }

    renderForm(form) {
        document.getElementById(form).style.display = "inline-block";
        var formsList = ["profile-display", "name-form", "email-form", "phone-form", "team-form"];
        var index = formsList.indexOf(form);
        formsList.splice(index, 1);
        for (var i in formsList) {
            document.getElementById(formsList[i]).style.display = "none";
        }
        this.closeAlert()
    }

    async changeName(e) {
        var tempFirst =  document.getElementById("firstNameInput").value;
        var tempLast = document.getElementById("lastNameInput").value;
        var requestOptions = {
            method: 'PUT',
            url: '/endzone/account/user/profile/update',
            headers: { 'Content-Type': 'application/json' },
            data: JSON.stringify({"first_name": tempFirst, "last_name": tempLast})
        };

        await axios(requestOptions).then((response) => {
            if (response.status === 200) {
                this.setState({firstName: tempFirst, lastName: tempLast});
                this.SuccessMessage(response.data);
            } else {
                this.FailureMessage(response.data);
            }
        }).catch((error) => this.FailureMessage("Failure connecting to database."));
    }

    async changeEmail(e) {
        var tempEmail =  document.getElementById("emailInput").value;
        var requestOptions = {
            method: 'PUT',
            url: '/endzone/account/user/profile/update',
            headers: { 'Content-Type': 'application/json' },
            data: JSON.stringify({"email": tempEmail})
        };

        await axios(requestOptions).then((response) => {
            if (response.status === 200) {
                this.setState({email: tempEmail});
                this.SuccessMessage(response.data);
            } else {
                this.FailureMessage(response.data);
            }
        }).catch((error) => this.FailureMessage("Failure connecting to database."));
    }

    async changePhone(e) {
        var tempPhone =  this.state.input;
        var requestOptions = {
            method: 'PUT',
            url: '/endzone/account/user/profile/update',
            headers: { 'Content-Type': 'application/json' },
            data: JSON.stringify({"phone": tempPhone})
        };

        await axios(requestOptions).then((response) => {
            if (response.status === 200) {
                this.setState({phone: tempPhone, input: ""});
                this.SuccessMessage(response.data);
            } else {
                this.FailureMessage(response.data);
            }
        }).catch((error) => this.FailureMessage("Failure connecting to database."));
    }

    async changeTeam(e) {
        var tempTeamCode = document.getElementById("teamInput").value;
        var tempTeamText = "Error";

        for (var i=0; i < this.state.teamsList.length; i++) {
            if (this.state.teamsList[i].value === tempTeamCode) {
                tempTeamText = this.state.teamsList[i].text;
            }
        }

        console.log(tempTeamText)
        var requestOptions = {
            method: 'PUT',
            url: '/endzone/account/user/profile/update',
            headers: { 'Content-Type': 'application/json' },
            data: JSON.stringify({"curTeam": tempTeamCode})
        };

        await axios(requestOptions).then((response) => {
            if (response.status === 200) {
                this.setState({curTeam: tempTeamCode});
                this.setState({curTeamText: tempTeamText});
                this.SuccessMessage(response.data);
            } else {
                this.FailureMessage(response.data);
            }
        }).catch((error) => this.FailureMessage("Failure connecting to database."));
    }

    closeAlert() {
        this.setState ({alert: {
            style: {display: "none"}}
        });
    }

    SuccessMessage(msg) {
        this.setState ({alert: {
            style: {display: "block"},
            severity: "success",
            userMessage: msg}
        });
    }

    FailureMessage(msg) {
        this.setState ({alert: {
            style: {display: "block"},
            severity: "error",
            userMessage: msg}
        });
    }

    handleEnterSubmit(event) {
        event.preventDefault();
    }


    render() {
        return(
            <div className="page-display">

                <EndzoneNav links={[{title: "Return to Hub", link: "/endzone/hub"},]}></EndzoneNav>
                <div className='alert-div' id="alert" style={this.state.alert.style}>
                    <Alert variant="filled" severity={this.state.alert.severity}
                            action={
                                <IconButton
                                    aria-label="close"
                                    color="inherit"
                                    size="large"
                                    onClick={() => {
                                        this.closeAlert();
                                    }}
                                >
                                    <CloseIcon fontSize="inherit"/>
                                </IconButton>
                            }>
                            {this.state.alert.userMessage}
                    </Alert>
                </div>
                <div className="body-container">
                    <div className="sidebar-nav">
                        <button id="profile-button" onClick={() => {this.renderForm("profile-display"); this.highlightButton("profile-button")}}>Profile</button>
                        <button id="name-button" onClick={() => {this.renderForm("name-form"); this.highlightButton("name-button")}}>Name</button>
                        <button id="email-button" onClick={() => {this.renderForm("email-form"); this.highlightButton("email-button")}}>Email</button>
                        <button id="phone-button" onClick={() => {this.renderForm("phone-form"); this.highlightButton("phone-button")}}>Phone Number</button>
                        <button id="team-button" onClick={() => {this.renderForm("team-form"); this.highlightButton("team-button")}}>Team</button>
                    </div>

                    <div className="middle-body-container">

                        <div className="profile-container" id="profile-display">
                            <div className="profile-display-item">
                                <p className="profile-description">First Name:</p>
                                <p className="profile-value">{this.state.firstName}</p>
                            </div>
                            <div className="profile-display-item">
                                <p className="profile-description">Last Name:</p>
                                <p className="profile-value">{this.state.lastName}</p>
                            </div>

                            <div className="profile-display-item">
                                <p className="profile-description">Email:</p>
                                <p className="profile-value">{this.state.email}</p>
                            </div>

                            <div className="profile-display-item">
                                <p className="profile-description">Phone:</p>
                                <p className="profile-value">{this.state.phone}</p>
                            </div>

                            <div className="profile-display-item">
                                <p className="profile-description">Current Team:</p>
                                <p className="profile-value">{this.state.curTeamText}</p>
                            </div>
                        </div>


                    
                        <form className="register-form" id="name-form" onSubmit={(e) => this.handleEnterSubmit(e)}>
                            <EndzoneTextInput label="First Name:" id="firstNameInput" classStyle={{"max-width": "100%"}} textStyle={{"font-size": "5vh"}} labelStyle={{"font-size": "5vh"}}></EndzoneTextInput>
                            <EndzoneTextInput label="Last Name:" id="lastNameInput" classStyle={{"max-width": "100%"}} textStyle={{"font-size": "5vh"}} labelStyle={{"font-size": "5vh"}}></EndzoneTextInput>     
                            <br/>
                            <button className="submit-button" type="button" onClick={(e) => this.changeName(e)}>Submit</button>
                        </form>
            

                        <form className="register-form" id="email-form" onSubmit={(e) => this.handleEnterSubmit(e)}>
                            <EndzoneTextInput label="Email:" id="emailInput" classStyle={{"max-width": "100%"}} textStyle={{"font-size": "5vh"}} labelStyle={{"font-size": "5vh"}}></EndzoneTextInput>
                            <br/>
                            <button className="submit-button" type="button" onClick={(e) => this.changeEmail(e)}>Submit</button>
                        </form>

                        <form className="register-form" id="phone-form" onSubmit={(e) => this.handleEnterSubmit(e)}>
                            <div className="phone-container">
                                <label className="phone-input-title">Phone Number:</label>
                                <PhoneInput defaultCountry="US" placeholder="Enter here"
                                    numberInputProps={{"style": {"font-size": "5vh", "display": "inline-block", "width": "auto", "font-family": "audiowide", "background": "#353b48", "color": "white", 
                                    "border": "1px solid #0984e3", "border-radius": "2vh", "margin-bottom": "1vh", "padding":"1vh"}}} 
                                    style={{"font-size": "5vh", "color": "white"}} onChange={(e) => this.setState({input: e})}
                                />
                            </div>
                            <br/>
                            <button className="submit-button" type="button" onClick={(e) => this.changePhone(e)}>Submit</button>
                        </form>
                        
                        <form className="register-form" id="team-form" onSubmit={(e) => this.handleEnterSubmit(e)}>
                            <EndzoneComboBox id="teamInput" label="Current Team:" options={this.state.teamsList} classStyle={{"max-width": "100%"}} labelStyle={{"font-size": "5vh"}} comboStyle={{"font-size": "5vh"}}></EndzoneComboBox>
                            <br/>
                            <button className="submit-button" type="button" onClick={(e) => this.changeTeam(e)}>Submit</button>

                        </form>
                    </div>
                </div>
            </div>
        );
    }
}



export default Profile;